.App {
  text-align: center;
  background: linear-gradient(
    180deg,
    rgba(40, 44, 52, 1) 0%,
    rgba(97, 218, 251, 1) 100%
  );
  min-height: 100vh;
}

.App-logo {
  height: 40px;
  margin: 5px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  padding: 20px 0 0 0;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header h1 {
  margin: 0 0 20px 0;
  font-size: 2.5rem;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
